
import { Icon, InlineIcon } from "@iconify/vue";
import plusIcon from "@iconify-icons/dashicons/plus-alt2";
import removeIcon from "@iconify-icons/dashicons/remove";

import { defineComponent } from "vue";

export default defineComponent({
  name: "ManageList",
  data() {
    return {
      inputText: "" as string | null,
      icons: {
        plus: plusIcon,
        remove: removeIcon,
      },
    };
  },
  props: {
    itemLabel: String,
    list: Array,
    pathPrefix: String,
  },
  components: {
    Icon,
  },
  methods: {
    addItem() {
      if (this.inputText) {
        this.$emit("item-added", {
          name: this.inputText,
        });
        // this.$refs.inputField;
        this.inputText = null;
      }
    },
    removeItem(item: any) {
      this.$emit("item-removed", item);
    },
    navigated(event: any) {
      this.$emit("navigated");
      const path = event.target.attributes[0].nodeValue;
      this.$router.push(path);
    },
  },
});
